@value todoGreen, todoLightPink, todoWhite from '../../common/colors.css';

.login {
  width: 30rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: todoWhite;
}

.list {
  width: 100%;
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 0.5rem;
}

.item {
  margin-bottom: 0.5rem;
}

.button {
  width: 100%;
  height: 2.5rem;
  font-size: 1.2rem;
  border-radius: 1.2rem;
  background-color: transparent;
  cursor: pointer;
  border: 0.2rem solid todoGreen;
  outline: 0;
}

.button:hover {
  background-color: todoLightPink;
}
